
.top_ulcss{
    list-style: none;
    width:50%;
    display: inline-block;
    height:inherit;
    display:flex;
    justify-content: flex-end;
    position: absolute;
    right:0;

}

.top_ulcss>li{
    display:inline-block;
    height:inherit;
    line-height: 40px;
    padding:0 20px 0 20px;
    box-sizing: border-box;
    cursor: pointer;
}
